





















































































import common from '../../common/common.js';
import {
	mapState
} from 'vuex';
var cmr = null;
export default {

	data() {

		return {
			isFix: false,
			isshow: false,
			src: '',
			showVideo: false,
			content: "",
			comlist: [],
			imageList: [],
			imgurlarr: [],
			lifevideo: "",
			lifecat: "",
			pushid:""
		}
	},
	computed: {
		...mapState(['user']),
	},

	onUnload() {
		this.src = '';
		//uni.hideLoading();
	},
	methods: {
		islogin: function() {
			if (this.user.user === null) {
				uni.reLaunch({
					url: '../../pages/manage/login'
				})
				return;
			}
		},
		/* getimglist: function(data) {
			let that = this;
			this.imageList = data;
			this.imageList.forEach(function(value, key) {
				//console.log(value);
				//that.uploadImage(value, 1);
			})
		}, */
		getcomlist: function() {

			let that = this;
			//console.log(this.user.user.token);

			uni.request({
				url: common.hosturl + "index/getmessage.html",
				method: "POST",
				data: {
					token: this.user.user.token,
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: function(res) {

					//console.log(JSON.stringify(res.data))
					if (res.data.code == 0) {

						that.comlist = res.data.list;

					} else {
						uni.showToast({
							icon: 'none',
							title: '出错啦，请联系客服'
						});
					}
				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}
			})
		},

		uploadImage: function(e, type) {
			uni.showLoading({
				title: '上传中..',
				mask: true
			});

			let that = this;
			//console.log(e);
			const uploadTask = uni.uploadFile({
				url: common.hosturl + "Upload/uploadfile.html",
				filePath: e,
				name: "file",
				formData: {
					token: this.user.user.token
				},
				success: (uploadFileRes) => {
					let res = JSON.parse(uploadFileRes.data);
					if (res.code == 0) {

						if (type == 1) {
							this.imgurlarr = this.imgurlarr.concat(res.fileurl);
							if (this.imgurlarr != "") {
								uni.hideLoading();
							}

						} else {
							this.lifevideo = res.fileurl;
							if (this.lifevideo != "") {
								uni.hideLoading();
							}

							console.log(this.lifevideo);


						}


						//console.log(JSON.stringify(this.imgurlarr));
					} else {


					}


				},
			});

		},

		postcontent: function() {
			if (this.imgurlarr == "") {
				uni.showToast({
					icon: 'none',
					title: '请上传图片'
				});
				return;
			}

			if (this.content == "") {
				uni.showToast({
					icon: 'none',
					title: '请选择备注信息'
				});
				return;
			}
			
			if (this.lifevideo == "") {
				uni.showToast({
					icon: 'none',
					title: '请上传视频'
				});
				return;
			}

			//let lifeimage=this.imgurlarr.join("|");

			let data = {
				'lifeimage': this.imgurlarr,
				'token': this.user.user.token,
				'lifecontent': this.content,
				'lifecat': this.lifecat,
				'lifevideo': this.lifevideo,
				'pushid':this.pushid,
			
			};
			var that = this;
			uni.request({
				url: common.hosturl + "life/savelife.html",
				method: "post",
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: data,
				success: function(res) {
					console.log(res.data);
					if (res.data.code == 0) {
						uni.showToast({
							icon: 'none',
							title: res.data.msg,
							success: function() {

								uni.redirectTo({
									url: "tranInfo?lifecat=" + that.lifecat
								})
							}

						});

					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});

					}

				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}

			})
		},
		delImage: function(e) {
			console.log(e);
			this.imageList.splice(e, 1);
			this.imgurlarr.splice(e, 1);
			console.log(this.imgurlarr);
		},
		showmessage: function(e) {
			this.content = this.comlist[e].content;
			this.isFix = false;

		},
		chooseVideo: function() {

			var that = this;
			var VIDEOZOOM = 100;
			var MediaStore = plus.android.importClass("android.provider.MediaStore");
			var Intent = plus.android.importClass("android.content.Intent");
			// 导入后可以使用new方法创建类的示例对象
			var intent = new Intent("android.media.action.VIDEO_CAPTURE");
			intent.putExtra("android.intent.extra.videoQuality", 1); //0 means low quality, 1 means high quality
			//intent.putExtra("android.provider.MediaStore.EXTRA_OUTPUT", url);
			intent.putExtra("android.intent.extra.durationLimit", 10); //设置录像时间

			var main = plus.android.runtimeMainActivity();
			main.startActivityForResult(intent, VIDEOZOOM);
			//获取返回参数
			main.onActivityResult = function(requestCode, resultCode, data) {
				var context = main;
				plus.android.importClass(data);
				var uri = data.getData();
				var resolver = context.getContentResolver();
				plus.android.importClass(resolver);
				var cursor = resolver.query(uri, null, null, null, null);
				plus.android.importClass(cursor);
				cursor.moveToFirst();
				var column = cursor.getColumnIndexOrThrow(MediaStore.Video.Media.DATA);
				// 获取录制的视频路径
				var filePath = cursor.getString(column);
				that.src = filePath;
				//	console.log(that.src);
				that.isshow = !that.isshow;
				that.uploadImage(filePath, 2);


			};
		},

		chooseImage: async function() {
			let imgcount = 3;
			let that = this;
			if (this.imageList.length == 3) {
				let isContinue = await this.isFullImg();
				if (!isContinue) {
					return false;
				}
			}
			uni.chooseImage({
				count: imgcount - this.imageList.length,
				sourceType: ["camera"],
				success: (res) => {
					//console.log(JSON.stringify(res));
					//console.log(res.tempFiles.size);
					this.imageList = this.imageList.concat(res.tempFilePaths);
					res.tempFilePaths.forEach(function(value, key) {
						that.uploadImage(value,1);
					})
				}
			})
		},
		isFullImg: function() {
			return new Promise((res) => {
				uni.showModal({
					content: "最多只能选择3张图片,是否清空现有图片？",
					success: (e) => {
						if (e.confirm) {
							this.imageList = [];
							res(true);
						} else {
							res(false)
						}
					},
					fail: () => {
						res(false)
					}
				})
			})
		},

		previewImage: function(e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		}
	},
	onLoad: function(e) {
		//console.log(JSON.parse(e.param));
		let param=JSON.parse(e.param);
		this.lifecat = param.lifecat;
		this.pushid=param.pushid;
		this.islogin();
		//console.log(this.lifecat);
		//console.log(param.lifecat);
	},
	onShow: function() {

		this.getcomlist();
	}
}



